.jumbotron{
    background-image: url("https://images.unsplash.com/photo-1506784693919-ef06d93c28d2?ixlib=rb-0.3.5&ixid=eyJhcHBfaWQiOjEyMDd9&s=4501e1c9fb53eb2c6c52be4038f53e34&auto=format&fit=crop&w=1050&q=80");
    background-repeat: no-repeat;
    background-position: center;
    background-size: cover;
    position: relative;
